import axios from 'axios'
export default {
    data(){
        return {
            lists:[]
        }
    },
    created(){
        let localLists = window.sessionStorage.getItem('usbox')
        if (localLists) {
            this.lists = JSON.parse(localLists)
        } else {
            this.getData()
        }
    },
    methods:{
        getData(){
            axios.get('https://api.douban.com/v2/movie/us_box').then(res=>{
                res.data.subjects.forEach((item)=>{
                    this.lists.push(item.subject)
                    console.log(this.lists)
                })
                let localLists = JSON.stringify(this.lists)
                window.sessionStorage.setItem('usbox',localLists)
            }).catch(error=>{
                console.log(error)
            })    
        }       
    }
}


